@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.edgtf-comment-holder {
	@include edgtfRelativeHolderLayout();
	padding: 0;
	box-sizing: border-box;

	.edgtf-comment-holder-inner {
		padding: 53px 0 20px;
	}

	.edgtf-comment {

		&.edgtf-pingback-comment {

			.edgtf-comment-text {
				padding: 0;
			}
		}
	}

	.edgtf-comment-list {
		list-style: none;
		position: relative;
		z-index: 150;
		padding: 0;
		margin: 0;

		li {
			@include edgtfRelativeHolderLayout();
			margin: 25px 0 0;
			padding: 29px 0 0;
		}

		> li:first-child {
			margin: 0;
			padding: 0;
			border: 0;
		}

		.children {
			padding: 0 0 0 81px;
		}
	}

	.edgtf-comments-title {
		margin-bottom: 21px;

		> * {
			margin: 0;
		}
	}

	.edgtf-comment-image {
		position: relative;
		display: block;
		top: 10px;
		left: 0;
		width: 65px;
		height: 65px;
		float: left;

		img {
			display: block;
			border-radius: 4em;
		}
	}

	.edgtf-comment-text {
		padding: 0 0 0 100px;
		min-height: 82px;

		.edgtf-comment-info {
			@include edgtfRelativeHolderLayout();
			margin: 6px 0 7px;
			width: auto;
		}

		.replay,
		.comment-reply-link,
		.comment-edit-link {
			float: right;
			margin-left: 10px;
			font-size: 16px;
			color: $default-heading-color;
			font-weight: 800;
			font-family: $default-heading-font;
			text-transform: capitalize;
			line-height: 40px;

			&:after {
				content: "\f054";
				font-family: 'FontAwesome';
				font-size: 12px;
				margin-left: 3px;
			}

			&:hover {
				color: $first-main-color;
			}
		}

		.edgtf-comment-name {
			display: block;
			margin: 0;
			letter-spacing: 0.1em;
		}

		.edgtf-comment-date {
			display: block;
			margin: 0;
			font-size: 14px;
			font-family: $third-heading-font;
			font-style: italic;
			color: $third-heading-color;
			margin-top: 5px;
		}

		.edgtf-text-holder p {
			margin: 4px 0 7px;
		}

		.comment-respond {
			margin: 20px 0;

			.comment-reply-title {
				margin: 0 0 10px;
				font-size: 14px;
			}

			.logged-in-as {
				margin: 0 0 8px;
			}
		}

		#cancel-comment-reply-link {
			margin-left: 10px;
			font-size: 12px;
			color: $first-main-color;

			&:hover {
				color: $default-heading-color;
			}
		}

		ul, ol {

			li {
				display: list-item;
			}
		}

		li {
			margin: 0 !important;
			padding: 0 !important;
			border: 0 !important;
		}

		ul {
			list-style-type: disc;
			ul {
				list-style-type: circle;

				ul {
					list-style-type: square;
				}
			}
		}
	}
}

.edgtf-comment-form {
	padding: 0;
	clear: both;
	box-sizing: border-box;

	.edgtf-comment-form-inner {
		padding: 45px 0 78px;
	}

	.comment-reply-title {
		margin: 0 0 37px;
	}

	.logged-in-as {
		margin: 0 0 8px;
	}
}

@include ipad-landscape {

	.edgtf-comment-holder {

		.edgtf-comment-list {

			.children {
				padding: 0 0 0 40px;
			}
		}
	}
}

@include ipad-portrait {

	.edgtf-comment-holder {

		.edgtf-comment-list {

			.children {
				padding: 0;
			}
		}
	}
}

@include phone-landscape {

	.edgtf-comment-holder {

		.edgtf-comment-image {
			float: none;
			margin: 0 0 20px;
			top: 0;
		}

		.edgtf-comment-text {
			padding: 0;

			.edgtf-comment-date,
			.edgtf-comment-name {
				float: none;
			}

			.replay,
			.comment-reply-link,
			.comment-edit-link {
				float: none;
				margin: 0 10px 0 0;
			}

			.edgtf-comment-info {
				display: block;
			}
		}
	}
}

@include phone-portrait {

	.edgtf-comment-holder {

		.edgtf-comment-image {
			float: none;
		}

		.edgtf-comment-text {
			padding: 20px 0 0;
			min-height: inherit;
		}
	}
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.edgtf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;

		footer & {
			margin: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include edgtfButtonDefaultStyle();
	@include edgtfButtonMediumParams();
	@include edgtfButtonSolidColor();
	cursor: pointer;

	&:hover {
		color: #fff;
		background-color: #04dbdf;
	}
}

.edgtf-cf7-bordered .wpcf7-form-control.wpcf7-submit.edgtf-cf7-submit {
	position: relative;
	background: transparent !important;
	z-index: 10;
}

.edgtf-cf7-bordered .edgtf-btn.edgtf-dir-aware-hover .edgtf-button-overlay-holder .edgtf-button-overlay {
	height: 1300px;
	width: 1300px;
}

#submit_comment {
	width: 100%;
	background-color: $second-main-color;
}

p.form-submit {
	margin: 0;
}

p.form-submit {
	margin: 30px 0 0;
	position: relative;

	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		box-shadow: 3px 4px 5px 0 rgba(0, 0, 0, .1);
		z-index: -1;
		@include edgtfTransition(all .3s cubic-bezier(.51, .15, .23, .96));
		will-change: box-shadow;
	}

	&:hover {
		background-color: #04dbdf;
		&:after {
			box-shadow: 4px 6px 5px 0 rgba(0, 0, 0, .2);
		}
	}
}

#respond textarea,
.edgtf-style-form textarea,
.wpcf7-form textarea {
	display: block;
	padding: 16px 18px;
}

.edgtf-cf7-bordered {

	.edgtf-vertical-split-slider & {
		.edgtf-btn.edgtf-btn-solid {
			color: #fff;
			background-color: $second-main-color;
			border: 1px solid transparent;

			&.edgtf-dir-aware-hover .edgtf-button-overlay-holder .edgtf-button-overlay {
				background-color: $third-main-color;
			}
		}
	}

	input[name="your-subject"] {
		margin-bottom: 0;
	}

	.wpcf7-form-control {

		&.wpcf7-submit.edgtf-cf7-submit {
			padding: 0 4px 0 0;

			+ .ajax-loader {
				display: none;
			}
		}
	}
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.edgtf-owl-slider {
	visibility: hidden;

	.owl-stage-outer {
		z-index: 1;
	}

	&.edgtf-slider-is-moving {

		.owl-item {

			.edgtf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}

	&.edgtf-slider-has-both-nav {

		.owl-nav {

			.owl-prev,
			.owl-next {
				$pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
				@include edgtfTransform(translateY($pos));
			}
		}
	}

	&.edgtf-slider-hide-pagination .owl-dots {
		display: none;
	}

	.owl-nav {

		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: #231f20;
			@include edgtfTransform(translateY(-50%));
			@include edgtfTransition(color .2s ease-out);

			&:hover {
				color: $first-main-color;
			}

			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 32px;
				line-height: 1;

				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}

		.owl-prev {
			left: 30px;
		}

		.owl-next {
			right: 30px;
		}
	}

	.owl-dots {
		text-align: center;
		margin: 16px 0 0;

		.owl-dot {
			display: inline-block;
			vertical-align: middle;

			span {
				display: inline-block;
				vertical-align: middle;
				width: 6px;
				height: 6px;
				margin: 0 7px;
				border-radius: 3em;
				background-color: #231f20;
				box-sizing: border-box;
				@include edgtfTransition(background-color .2s ease-out, border .2s ease-out);
			}

			&:hover,
			&.active {

				span {
					width: 10px;
					height: 10px;
				}
			}
		}
	}

	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}

	+ .edgtf-slider-thumbnail {
		display: inline-block;
		vertical-align: middle;
		list-style: none;
		margin: 20px -10px 0;

		> .edgtf-slider-thumbnail-item {
			float: left;
			padding: 0 10px;
			margin-bottom: 20px;
			box-sizing: border-box;
			line-height: 0;
			cursor: pointer;

			img {
				border: 2px solid transparent;
				@include edgtfTransition(border-color 0.2s ease-out);
			}

			&.active {
				img {
					border: 2px solid $first-main-color;
				}
			}
		}

		$thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

		@for $i from 0 to length($thumbnail_columns_number) {
			&.edgtf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

				.edgtf-slider-thumbnail-item {
					width: 100% / ($i+2);

					@include ipad-landscape {
						width: 33.3333%;
					}
				}
			}
		}

	}

}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

	.edgtf-wrapper {
		padding: 0 !important;
	}

	.edgtf-content {
		height: 100vh;
	}

	&.admin-bar {

		.edgtf-content {
			height: calc(100vh - 32px);
		}
	}

	.edgtf-content-inner {
		@include edgtfTableLayout();
	}

	.edgtf-page-not-found {
		@include edgtfTableCellLayout();
		padding: 0 20%;
		text-align: center;

		.edgtf-404-title {
			margin: 0;
			font-size: 183px;
			color: #512da8;
		}

		.edgtf-404-subtitle {
			margin: 19px 0 0;
			font-size: 28px;
			font-family: $default-heading-font;
			font-weight: 800;
			text-transform: none;
			color: $default-heading-color;
		}

		.edgtf-404-text {
			margin: 19px auto 0;
			width: 50%;
		}

		.edgtf-btn {
			margin: 24px 0 0;

			&.edgtf-btn-light-style {
				color: #585858;
				background-color: #fff;
				border-color: #fff;

				&:hover {
					color: #fff !important;
					background-color: transparent !important;
					border-color: #fff !important;
				}
			}
		}
	}

	@include laptop-landscape-mac {
		.edgtf-page-not-found {
			padding: 12% 20%;
		}
	}

	@include ipad-landscape {

		.edgtf-page-not-found {
			padding: 25% 20% 15%;
		}

		.edgtf-content {
			height: 100%;
			min-height: 100vh;

			.edgtf-404-text {
				width: 75%;
			}
		}

		&.admin-bar {

			.edgtf-content {
				height: 100%;
				min-height: calc(100vh - 32px);
			}
		}
	}

	@include ipad-portrait {

		.edgtf-page-not-found {
			padding: 45% 30px 20px;
		}
	}

	@include phone-landscape {

		.edgtf-content {

			.edgtf-404-text {
				width: 100%;
			}
		}

		.edgtf-page-not-found {
			padding: 30% 30px 20%;
		}
	}

	@include phone-portrait {

		.edgtf-page-not-found {
			padding: 65% 30px 20%;

			.edgtf-404-subtitle {
				margin: 55px 0 0;
			}
		}
	}
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#edgtf-back-to-top {
	position: fixed;
	width: 41px;
	right: 25px;
	bottom: 25px;
	margin: 0;
	z-index: 10000;
	opacity: 0;
	visibility: hidden;
	overflow: hidden;

	&.off {
		opacity: 0;
		visibility: hidden;
		@include edgtfTransition(opacity 0.15s ease, visibility .0s .15s);
	}

	&.on {
		opacity: 1;
		visibility: visible;
		@include edgtfTransition(opacity 0.3s ease);
	}

	.edgtf-icon-stack {
		position: relative;
		display: block;
		width: 41px;
		height: 41px;
		line-height: 42px;
		font-size: 21px;
		text-align: center;
		color: #fff;
		background-color: $first-main-color;
		overflow: hidden;
		@include edgtfTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);

		i {
			position: relative;
			z-index: 20;
		}
	}

	.edgtf-btt-background-holder {
		@include edgtfAbsoluteHolderLayout();

		.edgtf-btt-background {
			position: absolute;
			border-radius: 50%;
			z-index: 5;
			width: 200px;
			height: 200px;
			background-color: $default-heading-color;
			transform-origin: center;
			@include edgtfTransitionTransform(.2s cubic-bezier(.5, .15, .33, .88));
			@include edgtfTransform(translate(-50%, -50%) scale(0));
		}
	}

	span span {
		display: block;
		line-height: inherit;
	}

	&:hover {
		.edgtf-btt-background {
			@include edgtfTransform(translate(-50%, -50%) scale(1));
		}
	}
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.edgtf-self-hosted-video-holder {

	.edgtf-video-wrap {

		.mejs-poster,
		.mejs-container,
		.mejs-overlay-play {
			height: 100% !important; /*!important because of the inline style*/
			width: 100% !important; /*!important because of the inline style*/
		}
	}
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
   #Row Background styles - begin
   ========================================================================== */
.edgtf-row-has-background-text {
	position: relative;
}

.edgtf-row-background-text-holder {
	position: absolute;
	display: block;
	width: 100%;
	height: 100%;
	top: 0;
	left: -40px;

	.edgtf-row-background-text-wrapper {
		@include edgtfTableLayout();

		.edgtf-row-background-text-wrapper-inner {
			display: table-cell;
			vertical-align: middle;
			font-family: $default-heading-font;
			color: rgba(#512da8, 0.06);
			font-size: 380px;
			letter-spacing: -0.04em;
			padding-top: 4.8%;

			.edgtf-row-background-text-1,
			.edgtf-row-background-text-2 {
				font-weight: 800;
				line-height: 1;
			}
		}

		@include ipad-landscape {
			display: none;
		}

		&.edgtf-row-background-text-animation {
			visibility: hidden;
			@include edgtfTransitionTransform(.75s cubic-bezier(.5, .15, .33, .88));
			@include edgtfTransform(translate3d(-100%, 0, 0));

			.no-touch & {
				.edgtf-char-mask {
					display: inline-block;
					vertical-align: middle;
				}
			}

			&.edgtf-show {
				visibility: visible;
				@include edgtfTransform(translate3d(0%, 0, 0));
			}
		}
	}
}

/* ==========================================================================
   #Row Background styles - end
   ========================================================================== */