/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */

/* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */

aside.edgtf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {

        &:last-child {
            margin: 0 !important;
        }
	}
}

.wpb_widgetised_column {
    position: relative;

    .widget {
	
	    &:last-child {
		    margin: 0 !important;
	    }
    }
}
/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 30px;
	
	.edgtf-widget-title {
		margin: 0 0 11px;
		
		a {
			color: inherit;
		}
	}
	
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
		
		li {
			margin: 0 0 10px;
			
			&:last-child {
				margin: 0;
			}
		}
	}

    /*
    *   Specific styles
    */
    &.widget_text {

        img {
            display: block;
        }
    }

    #wp-calendar {
        width: 100%;
        text-align: center;

        caption {
            color: $default-heading-color;
            margin: 0 0 15px;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: #fff;
            background-color: $first_main_color;

            a {
                color: inherit;
            }
        }
    }

    &.widget_rss {

        .edgtf-widget-title {

            .rsswidget {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .rss-widget-icon {
            display: inline-block;
        }

        ul {

            li {
                margin: 0 0 25px;

                .rss-date {
                    display: block;
                }
            }
        }
    }

    &.widget_search {

        form {
            @include edgtfRelativeHolderLayout();
            clear: both;
            margin-bottom: 21px;

            > div {
                @include edgtfRelativeHolderLayout();
                box-sizing: border-box;
                border-bottom:3px solid #d6d6d7;

                &:focus-within {
                    border-color: $second-main-color;
                }
            }
        }

        input,
        button {
            position: relative;
            display: inline-block;
            vertical-align: top;
            float: left;
            height: 52px;
            font-weight: 400;
            font-size: 15px;
            line-height: 32px;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            outline: 0;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include edgtfTransition(color .2s ease-out);
            color:$third-heading-color;
            font-style: italic;
            font-family: $third-heading-font;
        }

        input {
            width: calc(100% - 40px);
            padding: 14px 0 10px 16px;
	
	        @include edgtfPlaceholder {
		        color: inherit;
	        }
        }

        button {
            width: 40px;
            padding: 7px 8px 0 0;
            font-size: 19px;
            color:$third-heading-color;
            cursor: pointer;
            @include edgtfTransition(color .2s ease-in-out);

            &:hover {
                color: $first-main-color;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }
    }

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_comments,
    &.widget_nav_menu {

        ul {

            li {
                margin: 0;
                padding: 3px 0;
                font-family: $third-heading-font;
                font-size: 15px;

                a {
                    color:$third-heading-color;
                    font-style: italic;

                    &:hover{
                        color: $first-main-color;
                    }
                }

                &:last-child {
                    border: none;
                }

                ul {
                    margin: 4px 0 0;
                    padding: 0 0 0 10px;

                    li {

                        &:last-child {
                            padding: 4px 0 0;
                        }
                    }
                }
            }
        }
    }

    &.widget_recent_entries {
	
	    .edgtf-widget-title {
            margin: 0 0 15px;
        }

        ul {

            li {
                margin: 0 0 5px;
                font-family: $third-heading-font;
                font-size: 15px;

                a {
                    color:$third-heading-color;
                    font-style: italic;

                    &:hover{
                        color:$first-main-color;
                    }
                }

                > span {
                    display: block;
	                font-size: 14px;
                }
            }
        }
    }

    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            width: 100%;
            max-width: 100%;
            height: 54px;
            padding: 0 18px;
            line-height: 54px;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            font-family: inherit;
            font-size: inherit;
            font-style: inherit;
            font-weight: inherit;
            outline: none;
            box-sizing: border-box;
        }
    }

    &.posts_holder {

        ul {
            
            li {

                &.page_item_has_children {

                    & > a {
                        display: block;
                    }

                    & > ul.children {
                        padding-left: 10px;
                    }
                }

                &.menu-item-has-children {

                    & > a {
                        display: block;
                    }

                    & > ul.sub-menu {
                        padding-left: 10px;
                    }
                }
            }
        }
    }

    &.widget_tag_cloud {


        .edgtf-widget-title {
            margin: 0 0 13px;
        }

        a {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            font-weight: 400;
            background-color: transparent;
            color:$third-heading-color;
            font-style: italic;
            font-size: 15px !important;
            font-family: $third-heading-font;
            margin-right: 5px;

            &:last-child {

                &:after {
                    display: none;
                }
            }

            &:hover{
                color:$first-main-color;
            }
        }
    }
}

.vc_wp_search {

    .widget {

        &.widget_search {

            form {

                > div {
                    background-color: #fff;
                    border: none;
                }
            }

            input {
                width: calc(100% - 66px);
                padding: 0 0 0 33px;
                font-size: 15px;
            }

            button {
                width: 66px;
                padding: 0 39px 0 9px;
            }
        }
    }
}

.edgtf-page-header,
.edgtf-top-bar,
.edgtf-sticky-header,
.edgtf-mobile-header {
    
    .widget {
        margin: 0;
        
        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_recent_comments,
        &.widget_rss,
        &.widget_calendar,
        &.widget_rss,
        &.widget_archive,
        &.widget_categories,
        &.widget_tag_cloud,
        &.widget_nav_menu,
        &.widget_search {
            display: none !important;
        }
    }
}

.edgtf-mobile-header {

    .widget {

        &.widget_text {
            display: none !important;
        }
    }
}

.edgtf-side-menu {

    .widget {
        color: #fff;

        &:last-child {
            margin: 0 !important;
        }

        h1, h2, h3, h4, h5, h6 {
            margin: 0;
            color: #fff;
        }

        .edgtf-widget-title {
            color: #fff;
            margin: 0 0 27px;
        }

        a {
            color: #fff;

            &:hover {
                color: $first-main-color;
            }
        }

        table thead tr,
        table tbody tr {
            border-color: #fff;
        }

        table tbody th {
            border-right-color: #fff;
        }

        /*
        *   Specific styles
        */
        #wp-calendar {

            caption {
                color: #fff;
            }
        }

        &.widget_rss {

            .edgtf-footer-widget-title {

                .rsswidget {
                    color: #fff;

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }
        }

        &.widget_search {

            form {

                > div {
                    border-color: #fff;
                }
            }

            input {
                color: #fff;

                @include edgtfPlaceholder {
                    color: inherit;
                }

                &[type='submit'] {
                    color: #fff;
                }
            }

            button {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: #fff;
                border-color: #fff;

                option {
                    background-color: #363636;
                }
            }

        }

        &.widget_tag_cloud {

            a {
                color: #fff;

                &:after {
                    color: inherit;
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        &.edgtf-social-icons-group-widget {
            .edgtf-social-icon-widget-holder {
                margin: 0 13px 0 0;
            }
        }
    }
}

.edgtf-page-footer {

    .widget {
	    color: #b1b2b2;
	
	    &:last-child {
		    margin: 0 !important;
	    }
	    
        h1, h2, h3, h4, h5, h6 {
            margin: 0;
	        color: #fff;
        }
	
	    .edgtf-widget-title {
		    color: #fff;
		    margin: 0 0 27px;
	    }
	
	    a {
		    color: #b1b2b2;
		
		    &:hover {
			    color: $first-main-color;
		    }
	    }
	    
        table thead tr,
        table tbody tr {
            border-color: #b1b2b2;
        }

        table tbody th {
            border-right-color: #b1b2b2;
        }

        /*
        *   Specific styles
        */
	    #wp-calendar {
		
		    caption {
			    color: #b1b2b2;
		    }
	    }
	
	    &.widget_rss {
		
		    .edgtf-footer-widget-title {
			
			    .rsswidget {
				    color: #fff;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	    
        &.widget_search {

            form {

                > div {
                    border-color: #b1b2b2;
                }
            }

            input {
                color: #b1b2b2;
	            
	            @include edgtfPlaceholder {
		            color: inherit;
	            }

                &[type='submit'] {
                    color: #b1b2b2;
                }
            }

            button {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: #b1b2b2;
                border-color: #b1b2b2;

                option {
                    background-color: #363636;
                }
            }

        }

        &.widget_tag_cloud {

            a {
                color: #b1b2b2;

                &:after {
                    color: inherit;
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        &.widget_text {
            margin: 0 0 22px;
        }

        .footer-custom-menu {
            ul {
                display: inline-block;
                border-bottom: 3px solid rgba(#fff, 0.26);
                padding: 22px 32px 14px 40px;

                @include phone-portrait {
                    padding: 0 32px 14px 32px;
                }

                li {
                    display: inline-block;
                    padding: 0 46px;
                    margin: 0;

                    @include ipad-portrait {
                        padding: 0 11px;
                    }

                    @include phone-portrait {
                        display: block;
                        padding: 0;
                    }

                    a {
                        cursor: pointer;
                        position: relative;
                        font-family: $default-heading-font;
                        font-weight: 800;
                        font-size: 16px;
                        letter-spacing: 0;
                        color: #fff;

                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }
            }
        }
    }

    .edgtf-icon-widget-holder {
        font-size: 26px;
        color: rgba(#fff, 0.26);
        margin: 0 14px;

        @include phone-portrait {
            margin: 0 10px;
        }

        &:hover {
            color: $first-main-color;
        }
    }
}

.edgtf-top-bar {

	a {
		color: #969696;

		&:hover {
			color: $first-main-color;
		}
	}

    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;

        p {
            margin: 0;
        }

        &.widget_text {
            margin: 0 8px 0 0;
        }

        &.widget_nav_menu {

            ul {
                clear: both;

                li {
                    float: left;
                    padding: 0 8px;
                    margin: 0;
                    line-height: 14px;
                    border-left: 1px solid #fff;
                }
            }
        }
    }
}
/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */